body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-mochiy-pop-p-one: "Mochiy Pop P One";
  --font-poppins: Poppins;
  --font-modern-antiqua: "Modern Antiqua";

  /* font sizes */
  --font-size-lg: 18px;
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-white: #fff;
  --color-silver: rgba(202, 195, 195, 0.5);
  --color-gray-100: #080710;
  --color-gray-200: rgba(255, 255, 255, 0.07);
  --color-gray-300: rgba(255, 255, 255, 0.1);
  --color-gainsboro: #e5e5e5;

  /* Paddings */
  --padding-mini: 15px;
  --padding-3xs-5: 9.5px;
  --padding-3xs: 10px;
  --padding-12xs: 1px;
  --padding-mini-5: 14.5px;

  /* Border radiuses */
  --br-base: 16px;
  --br-21xl: 40px;
  --br-8xs: 5px;
  --br-10xs: 3px;
}
